.App {
  text-align: center;


}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  --header_height: 50px;
  --title_height: 20px;

  position: fixed;
  z-index: 1;
  /* background-color: #222; */
  width : 100%;
  height: var(--header_height);
  padding: 0px;
  color: black;
  background: -webkit-gradient(linear,left top, left bottom,from(#ffffff), color-stop(#f6f4f7), to(#ffffff) );
  background: -webkit-linear-gradient(top,#ffffff, #f6f4f7, #ffffff );
  background: -o-linear-gradient(top,#ffffff, #f6f4f7, #ffffff );
  background: linear-gradient(to bottom,#ffffff, #f6f4f7, #ffffff );
}

.header-title {
  position: absolute;
  vertical-align: middle;
  height: inherit;

}

h2 {
    font-size: var(title_height);
    padding: auto;
    text-align: left;


}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



